@import url(https://fonts.googleapis.com/css2?family=Space+Mono:wght@400;700&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

html{font-family:'Space Mono', monospace;font-weight:700;color:#00494d;max-width:1440px;min-width:375px;margin:auto;background-color:#c5e4e7}html h1{font-size:30px;margin:1rem}html h5{padding:.5rem}body{background-color:#c5e4e7}body .logo{text-align:center;padding:5rem 0 3rem 0}body input[type=number]::-webkit-inner-spin-button,body input[type=number]::-webkit-outer-spin-button{-webkit-appearance:none;margin:0}.main{display:flex;justify-content:center;width:auto;background-color:white;margin:5rem;border-radius:20px}@media (max-width: 899px){.main{flex-flow:wrap;min-width:375px;margin:0}}body{max-width:1024px;min-width:375px;margin:auto}text{font-family:'Space Mono', monospace;font-weight:700;color:#00494d;margin:auto;background-color:#c5e4e7}

